/* Medium Devices (Tablets, 768px and up) */
@media only screen and (min-width: 768px) {
  #products-area {
    .products-content-wrap {
      .product-loadmore-btn {
        margin-top: 50px;
      }
    }
  }

  #we-are-best-area {
    .we-best-content-wrap{
      .single-we-best-item{
        .font-size(14);
      }
    }
  }

  .single-product-info{
    .product-information{
      flex-wrap: nowrap;
      justify-content: space-between;

      .information-item{
        width: auto;
      }
    }
  }
} // end media quaries